<template lang="">
	<div class="container">
		<h1>Latest Posts</h1>
		<div class="create-post">
			<label for="cteate-post">Say something</label>
			<input type="text" id="create-post" v-model="text" placeholder="create a post">
			<button @click="handleClick">Add post</button>
		</div>
		<hr>
		<p class="error" v-if="error">
			{{ error }}
		</p>
		<div class="post-container">
			<div class="post" v-for="(post, index) in posts" :key="post._id" @dblclick="{deletePost(post._id); sanitizePosts()}">
				{{ post.createdAt }}
				<p class="text">{{ post.title }}</p>
			</div>
		</div>
	</div>
</template>

<script>
import { ref } from 'vue'
import { onMounted } from 'vue'
import { getPosts, createPost, deletePost} from '../PostService'

export default {
	name : 'Post',
	setup() {
		const posts = ref([])
		const error = ref('')
		const text = ref('')

		const sanitizePosts = async () => {
			posts.value = await getPosts()
			posts.value.forEach(item => {
				item.createdAt = item.createdAt.slice(0,10)
			})
		}
		onMounted(() => {
			try {
				sanitizePosts()
			} catch (err) {
				error.value = err
			}
		})
		
		const handleClick = async () => {
			await createPost(text.value)
			sanitizePosts()
			text.value = ''
		}

		return { posts, error, text, handleClick, deletePost, sanitizePosts}
	}
}
</script>
<style>
.container {
	max-width: 800px;
	margin: 0 auto;
}

.error {
	border: 1px solid #ff5b5f;
	background: pink;
	padding: 10px;
	margin-bottom: 15px;	
}

.post {
	position: relative;
	border: 1px solid #5bd658;
	background-color: #bcffb8;
	padding: 10px 10px 30px 10px;
	margin-bottom: 15px;
}

.created-at {
	position: absolute;
	top: 0;
	left: 0;
	padding: 5px 15px;
	background-color: darkgreen;
}

.text {
	font-size: 22px;
	font-weight: 700;
	margin-bottom: 0;
}

.create-post {
	display: flex;
	justify-content: center;
}

label, button {
	margin: 0px 25px;
}
</style>